/* styles.css */

.container.home-page {
  background-color: #ffffff !important;
  /* Adicione !important se necessário */
  width: 380px;
  display: flex;
}

.card {
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 90%;
  width: 240px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.title {
  color: #931d89;
  font-size: 24px;
  margin-top: 20px;
}

.select {
  margin-top: 20px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #931d89;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.button {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px 20px;
  background-color: #931d89;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: #7a166d;
}

.number-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.number-item {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #931d89;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px;
  font-size: 16px;
}



.image {
  width: 60px;
  /* Define a largura para 150px */
  height: auto;
  /* Ajusta a altura proporcionalmente */
  border-radius: 8px;
  /* Opcional: bordas arredondadas */
}

h1 {
  margin-bottom: -20px;
}

.brand {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #fff;
  margin-top: 20px;
  margin-bottom: 10px;
}