body {
  background-color: #931D89;
  display: flex;
  /* Define o layout como flexbox */
  justify-content: center;
  /* Centraliza horizontalmente */
  align-items: center;
  /* Centraliza verticalmente */
  height: 100vh;
  /* Garante que o body ocupe toda a altura da tela */
  margin: 0;
  /* Remove as margens padrão do body */
  font-family: 'Roboto', sans-serif;
  /* Aplica Roboto como fonte padrão */

}

.app {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  height: 100vh;
  /* Ocupa a altura total da janela */
}

.menu {
  display: flex;
  justify-content: space-around;
  background-color: #931D89;
  padding: 10px;
  width: 96%;
  align-self: center;
}

.menu-item {
  color: white;
  text-decoration: none;
  font-size: 1rem;
  padding: 5px 10px;
}

.menu-item:hover,
.menu-item.active {
  background-color: #4C546D;
  border-radius: 5px;
}

.content {
  flex: 1;
  /* O conteúdo ocupa todo o espaço restante */
  display: flex;
  /* Importante para alinhar os filhos */
  justify-content: center;
  /* Centraliza horizontalmente */
  align-items: center;
  /* Centraliza verticalmente */
  background-color: #f5f5f5;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #931d89;
  height: 100%;

}

.banner {
  width: 40%;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  color: #931d89;
  text-align: center;
  padding: 12px;
}

.banner2 {
  width: 10%;
  position: fixed;
  bottom: 100px;
  background-color: #fff;
  color: red;
  text-align: center;
  padding: 12px;
  right: -5%;
}

.banner3 {
  flex: auto;
  width: 20%;
  position: fixed;
  bottom: 100px;
  background-color: #fff;
  color: red;
  text-align: center;
  padding: 12px;
  margin-left: 50%;
}